declare type Permission = "clipboard-read" | "clipboard-write"

const textarea = document.createElement( "textarea" );

textarea.style.position = "fixed";
textarea.style.opacity = "0";
textarea.style.top = "0";
textarea.style.left = "0";
textarea.style.zIndex = "-1";
textarea.style.outline = "0 none";
textarea.style.overflow = "hidden";
textarea.setAttribute( "tabindex", "-1" );
textarea.setAttribute( "autocomplete", "off" );
textarea.setAttribute( "wrap", "hard" );

export const ClipboardManager = {
    check: async( permission: Permission ) =>
    {
        const state = await navigator.permissions.query( { name: permission as PermissionName } );

        return state.state;
    },

    getText: async() =>
    {
        return await navigator.clipboard.readText();
    },

    setText: ( text: string ) =>
    {
        document.body.appendChild( textarea );
        textarea.focus();
        textarea.value = text;
        textarea.select();
        document.execCommand( "cut" );
    },
};