
export interface ClickOutSideArgs {
    eventElements: Array<HTMLElement>;
    initFunc?: ClickOutsideInitFunc;
    clickOutsideFunc?: ClickOutsideClickFunc;
}

export declare type ClickOutsideHandler = ReturnType<typeof createClickOutsideHandler>

export function createClickOutsideHandler( { eventElements, initFunc, clickOutsideFunc } : ClickOutSideArgs )
{
    const globalClickHandler = ( ev: Event ) =>
    {
        let tmp: HTMLElement | null = ev.target as HTMLElement;

        while ( tmp !== null )
        {
            if ( eventElements.indexOf( tmp ) !== -1 )
                return;

            tmp = tmp.parentElement;
        }

        if ( clickOutsideFunc ) clickOutsideFunc();
        handler.destroy();
    };

    const handler = {
        create: () =>
        {
            if ( initFunc ) initFunc();
            window.addEventListener( "scroll", globalClickHandler, true );
            window.addEventListener( "mousedown", globalClickHandler );
        },
        destroy: () =>
        {
            window.removeEventListener( "scroll", globalClickHandler, true );
            window.removeEventListener( "mousedown", globalClickHandler );
        },
    };

    return handler;
}