export function appendElement<T extends keyof HTMLElementTagNameMap>( parent: Element, tagName: T, ...classes: string[] )
{
    const element = document.createElement( tagName );

    classes.forEach( ( x ) => element.classList.add( x ) );

    parent.appendChild( element );

    return element;
}

export function hasClass( element: HTMLElement, cls: string, until: Element | null )
{
    let tmpElement: HTMLElement | null = element;

    while ( tmpElement && !tmpElement.classList.contains( cls ) )
    {
        if ( tmpElement === until )
            return null;

        tmpElement = tmpElement.parentElement;
    }

    return tmpElement;
}

export function getElement( selector: string )
{
    const element = document.querySelector( selector );

    if ( element )
        return element;
    else
        throw new Error( `There is no '${selector}'` );
}