import { PopoverType } from "./classNames";
import { createESCHideController } from "./keyBinder";

export interface PopoverArgs {
    selector?: string;
    element?: HTMLElement;
    type: PopoverType;
    escClose?: boolean;
    parentElement?: HTMLElement;
    onESC?: () => void;
    onInit?: ( element: HTMLElement, handler: ReturnType<typeof createPopover> ) => void;
}

export interface IRPopover {
    get element(): HTMLElement;
    show: () => void;
    hide: () => void;
    bringToTop: () => void;
}

const zIndexMapList: Record<PopoverType, IRPopover[]> = {
    floating: [],
    dialog: [],
    message: [],
    popover: [],
    toast: [],
};

const getZIndex = ( type: PopoverType, id: number ) =>
{
    if ( id >= 100 ) throw new Error( `z-index could not be set more 100` );
    return `calc(var(--z-index-${type}) + ${id * 100})`;
};

const updateZIndex = ( type: PopoverType ) =>
{
    zIndexMapList[type]
        .forEach( ( popover, id ) => popover.element.style.zIndex = getZIndex( type, id ) );
};

export const createPopover = ( { selector, element, type, onESC, escClose = true, parentElement, onInit }: PopoverArgs ) =>
{
    const popoverElement = ( element || document.querySelector( selector || "" ) ) as HTMLElement | undefined;

    if ( !popoverElement )
        throw new Error( "missing element to popover. Please check the parameters (selector or element)" );

    const hideEventHandler = ( ev: TransitionEvent ) =>
    {
        if ( ev.target !== popoverElement ) return;

        popoverElement.remove();
        popoverElement.removeEventListener( "transitionend", hideEventHandler );
    };

    const handler = {
        get element()
        {
            return popoverElement;
        },
        show: () =>
        {
            if ( popoverElement.classList.contains( "is-visible" ) ) return;
            if ( escClose ) escController.create();

            if ( parentElement )
            {
                parentElement.appendChild( popoverElement );
                setTimeout( () => popoverElement.classList.add( "is-visible" ), 0 );
            }
            else
                popoverElement.classList.add( "is-visible" );

            zIndexMapList[type].push( handler );
            updateZIndex( type );

            return handler;
        },
        hide: () =>
        {
            if ( escClose ) escController.destroy();

            popoverElement.classList.remove( "is-visible" );
            popoverElement.style.removeProperty( "z-index" );

            if ( parentElement )
                popoverElement.addEventListener( "transitionend", hideEventHandler );

            zIndexMapList[type] = zIndexMapList[type].filter( ( x ) => x !== handler );

            if ( onESC )
                onESC();

            return handler;
        },
        bringToTop: () =>
        {
            handler.hide();
            handler.show();
        },
    };
    const escController = createESCHideController( handler );

    onInit && onInit( popoverElement, handler );

    if ( parentElement && popoverElement.isConnected )
        popoverElement.remove();

    return handler;
};