import _ from "lodash";

import { registerGlobalKeyListener } from "./keyBinder";

export interface ESCHiding {
    hide: () => unknown,
    onESC?: ESCEvent
}

export type ESCEvent = () => void;

const popupStack: ESCHiding[] = [];

registerGlobalKeyListener( "Escape", () =>
{
    const pop = popupStack.pop();

    if ( pop )
    {
        pop.onESC?.call( pop );
        pop.hide();
    }
} );

export declare type ESCHideController = ReturnType<typeof createESCHideController>

export function createESCHideController( obj: ESCHiding )
{
    function getId()
    {
        return popupStack.indexOf( obj );
    }

    return {
        create: () =>
        {
            if ( getId() === -1 )
                popupStack.push( obj );
            else
                throw new Error( `Already created esc controller` );
        },

        destroy: () =>
        {
            const id = popupStack.indexOf( obj );

            if ( id !== -1 )
                popupStack.splice( id, 1 );
        },
    };
}