interface CreateNewIconArgs {
    svg: string;
    name: string;
    masked?: boolean;
}

export const createCustomIcon = ( ...iconList: CreateNewIconArgs[] ) =>
{
    const buffer: string[] = [];

    iconList.forEach( ( { svg, name, masked = false } ) =>
    {
        const base64 = btoa( svg );
        const className = `.ir-icon--${name}`;
        const varName = `--ir-icon--${name}`;

        buffer.push( `
[class^="ir-icon"], [class*=" ir-icon"], [class^="ir-icon"]::before, [class*=" ir-icon"]::before {
    ${varName}: url("data:image/svg+xml;base64,${base64}")
}` );

        if ( masked )
        {
            buffer.push( `
${className}::before {
    -webkit-mask-image: var(${varName});
    mask-image: var(${varName});
    background-image: none;
}` );
        }
        else
        {
            buffer.push( `
${className}::before {
    background: var(${varName}) 0 / cover;
    -webkit-mask-image: none;
    mask-image: none;
}` );
        }

    } );

    const styleElement = document.createElement( "style" );

    styleElement.innerHTML = buffer.join( '\n' );
    document.head.appendChild( styleElement );

};