import _ from 'lodash';

import { getCssText } from './css';

import clsLoading from "@components/loading/loading.classNames";

export interface LoadingArgs {
    contextElement: HTMLElement;
    onClick: ( _ev: MouseEvent ) => void;
    animated?: boolean;
    startDelay?: number;
}

const createLoading = () =>
{
    const loading = document.createElement( "div" );
    const items = document.createElement( "div" );

    loading.className = clsLoading.loading.blockElementName;
    items.className = clsLoading.loadingItems.blockElementName;

    _.range( 4 )
        .forEach( () =>
        {
            const i = document.createElement( "i" );

            i.className = clsLoading.loadingDotItem.blockElementName;

            items.appendChild( i );
        } );

    loading.appendChild( items );

    return loading;
};

export const createLoadingHandler = ( { contextElement, onClick, animated = false, startDelay = 0 }: LoadingArgs ) =>
{
    const loading = createLoading();
    const loadingScrollHandler = () => loading.isConnected && handler.updatePos();
    let scheduled = false;
    let timeout = 0;

    if ( onClick )
        loading.onclick = ( ev ) => onClick( ev );

    // TODO 부드럽게 나타나기 사라지기 (PF 깜빡거림 방지용)
    if ( animated === false )
        loading.classList.add( "has-no-animation" );

    const handler = {
        updatePos: () =>
        {
            const rect = contextElement.getBoundingClientRect();

            loading.style.cssText = getCssText( {
                top: `${rect.y}px`,
                left: `${rect.x}px`,
                width: `${rect.width}px`,
                height: `${rect.height}px`,
            } );

        },
        show: () =>
        {
            if ( scheduled ) return;

            scheduled = true;
            timeout = window.setTimeout( () =>
            {
                scheduled = false;
                timeout = 0;

                handler.updatePos();
                requestAnimationFrame( () => loading.classList.add( "is-visible" ) );

                contextElement.insertAdjacentElement( "afterend", loading );
                window.addEventListener( "scroll", loadingScrollHandler, true );
            }, startDelay );

        },
        _hide()
        {
            loading.remove();
            window.removeEventListener( "scroll", loadingScrollHandler, true );
        },
        hide()
        {
            if ( scheduled )
            {
                clearTimeout( timeout );
                return;
            }

            scheduled = false;
            timeout = 0;

            loading.classList.remove( "is-visible" );

            if ( animated )
                loading.addEventListener( "transitionend", () => this._hide(), { once: true } );
            else
                this._hide();
        },
    };

    return handler;
};