
declare type LogLevel = 0 | 1 | 2 | 3
const LOG_LEVEL_LABEL = [ "ERROR", "WARNING", "DEBUG", "INFO" ] as const;

const LOG_LEVEL_MAP = {
    ERROR: 0,
    WARNING: 1,
    DEBUG: 2,
    INFO: 3,
} as const;

export class Logger
{
    private id: string;

    // error = 0, warning = 1, debug = 2, info = 3
    private _logLevel: LogLevel;

    constructor( id: string, logLevel = "ERROR" as keyof typeof LOG_LEVEL_MAP )
    {
        this.id = id;
        this._logLevel = LOG_LEVEL_MAP[logLevel];
    }

    get logLevel()
    {
        return LOG_LEVEL_LABEL[this._logLevel];
    }

    set logLevel( logLevel: keyof typeof LOG_LEVEL_MAP )
    {
        this._logLevel = LOG_LEVEL_MAP[logLevel];
    }

    error( ...data: unknown[] )
    {
        if ( this._logLevel >= 0 )
            console.error( this.id, ...data );
    }

    warn( ...data: unknown[] )
    {
        if ( this._logLevel >= 1 )
            console.warn( this.id, ...data );
    }

    debug( ...data: unknown[] )
    {
        if ( this._logLevel >= 2 )
            console.debug( this.id, ...data );
    }

    info( ...data: unknown[] )
    {
        if ( this._logLevel >= 3 )
            console.info( this.id, ...data );
    }

}