import _ from "lodash";

export type GlobalKeyFunc = ( _ev: KeyboardEvent ) => void

const keyMap: Record<string, GlobalKeyFunc[]> = {};

document.addEventListener( "keyup", ( ev ) =>
{
    if ( keyMap[ev.key] )
        keyMap[ev.key].forEach( ( callBack ) => callBack( ev ) );
} );

export function registerGlobalKeyListener( key: string, callback: GlobalKeyFunc )
{
    if ( keyMap[key] )
        keyMap[key].push( callback );
    else
        keyMap[key] = [callback];
}

export function unregisterGlobalKeyListener( key: string, callback: GlobalKeyFunc )
{
    const id = keyMap[key].indexOf( callback );

    if ( id !== -1 )
        keyMap[key].splice( id, 1 );
}

export function clearGlobalKeyListener( key: string )
{
    delete keyMap[key];
}