import { classPrefix } from "../../settings";

export const StateList = Object.freeze( ["is-hidden", "is-dragging", "is-active", "is-expanded", "is-collapsed", "is-selected", "has-children", "is-visible", "is-disabled", "is-diff", "is-same", "is-readonly", "is-error", "is-resizing"] as const );

export declare type StateType = typeof StateList[number];

export interface BEMClassArgs<T_Modifier, T_State> {
    block: string
    element?: string
    modifier?: T_Modifier[]
    state?: readonly T_State[]
}

export function BEMClass<T_Modifier extends string, T_State extends StateType = never>( { block, element = "", modifier = [], state = [] }: BEMClassArgs<T_Modifier, T_State> )
{
    const blockElementName = element ? `${classPrefix}${block}__${element}` : `${classPrefix}${block}`;

    const obj = {
        blockElementName,
        toString: () => blockElementName,
    };

    modifier.forEach( ( modi ) => Object.defineProperty( obj, modi, { get: () => `${blockElementName}--${modi}` } ) );
    state.forEach( ( state ) => Object.defineProperty( obj, state, { get: () => state } ) );

    return obj as typeof obj & {
        [T in T_Modifier]: `${typeof blockElementName}--${T}`;
    } & {
        [T in T_State]: T
    };
}